import { createMuiTheme } from '@material-ui/core';

const theme = createMuiTheme();
export default createMuiTheme({
    /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ overrides ~~~ */
    overrides: {
        MuiCssBaseline: {
            '@global': {
                html: {
                    minHeight: '100vh',
                    fontSize: '14px',
                    lineHeight: '1.15',
                },
                body: {
                    minHeight: 'inherit',
                    fontSize: '1rem',
                    fontWeight: '400',
                    lineHeight: 'inherit',
                    backgroundColor: theme.palette.background.default,

                },
                '#root': {
                    minHeight: 'inherit',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center',
                    alignItems: 'center',
                },
            },
        },
    },
});